<template>
    <a-layout style="padding: 0 0 0 2px;">
        <a-layout-header style=" background:#fff;padding: 14px;">
            <a-menu mode="horizontal">
                <a-menu-item key="1" @click="getListContest('')">
                    
                </a-menu-item>
                <a-sub-menu key="sub1" title="">
                    <a-menu-item key="2" @click="getListContest('popular=1')"></a-menu-item>
                    <a-menu-item key="3" @click="getListContest('popular=2')"></a-menu-item>
                </a-sub-menu>
                <a-sub-menu key="sub2" title="">
                    <a-menu-item key="4" @click="getListContest('contentPower=1')"></a-menu-item>
                    <a-menu-item key="5" @click="getListContest('contentPower=2')">VIP</a-menu-item>
                </a-sub-menu>
                <a-sub-menu key="sub3" title="">
                    <a-menu-item key="6" @click="getListContest('contentType=1')"></a-menu-item>
                    <a-menu-item key="7" @click="getListContest('contentType=2')"></a-menu-item>
                </a-sub-menu>
                <a-sub-menu key="sub4" title="">
                    <a-menu-item key="8" @click="getListContest('contestLevel=1')"></a-menu-item>
                    <a-menu-item key="9" @click="getListContest('contestLevel=2')"></a-menu-item>
                    <a-menu-item key="10" @click="getListContest('contestLevel=3')"></a-menu-item>
                </a-sub-menu>
            </a-menu>
        </a-layout-header>
        <a-layout-content :style="{ background: '#fff', padding: '14px 24px', margin: 0, minHeight: '280px' }">
            <a-list itemLayout="vertical" size="large" :pagination="pagination" :dataSource="listData">
                <a-list-item slot="renderItem" slot-scope="item, index" key="item.title">
                    <a-tag slot="extra" color="#f50">{{item.contestReward}}</a-tag>
                    <a-list-item-meta>
                        <div slot="description">
                            <p>{{item.contestIntroduce}}</p>
                            <a-row >
                                <div :style="{color:item.color}">
                                <a-col span="8">
                                    <span>
                                        <a-icon type="clock-circle" style="margin-right: 8px" />{{ item.tips }}
                                    </span>
                                </a-col>
                                <a-col span="7">
                                    <span v-if="time[index]">{{ time[index].day }}{{ time[index].hour }}{{ time[index].minute }}{{ time[index].second }}</span>
                                    <span v-else></span>
                                </a-col>
                                <a-col span="6">
                                    <span>
                                        <a-icon type="clock-circle" style="margin-right: 8px" />
                                        <span v-if="item.color === '#87d068'">&nbsp;|&nbsp;</span>
                                        <span v-else-if="item.color === 'orange'">&nbsp;|&nbsp;</span>
                                        <span v-else-if="item.color === '#f50'">&nbsp;|&nbsp;</span>
                                        <span v-else>&nbsp;|&nbsp;</span>
                                    </span>
                                </a-col>
                                </div>
                                <a-col span="3">
                                    <span>
                                        <a-icon type="clock-circle" style="margin-right: 8px" />
                                        <span v-if="item.contestLevel  === 1"></span>
                                        <span v-else-if="item.contestLevel === 2"></span>
                                        <span v-else></span>
                                    </span>
                                </a-col>
                            </a-row>
                        </div>
                        <!-- query 1. url 2. -->
                        <!-- <router-link slot="title" :to="{name:'gameContent', query:{teamId:item.id}}">{{item.contestName}}<a-icon type="share-alt" /></router-link> -->
                        
                        <!-- query 1. url 2. -->
                        <router-link slot="title" :to="'gameContent/details?contestId='+item.id">{{item.contestName}}<a-icon type="share-alt" /></router-link>

                        <!-- params 1. url 2.  3. name 'details'-->
                        <!-- <router-link slot="title" :to="{name:'details', params:{teamId:item.id}}">{{item.contestName}}<a-icon type="share-alt" /></router-link> -->
                        
                        <!-- params1. url; 2.  3.  path  path:'details/:teamId';-->
                        <!-- <router-link slot="title" :to="'gameContent/details/'+item.id">{{item.contestName}}<a-icon type="share-alt" /></router-link> -->
                        <a-badge slot="avatar" :count="item.contentPower === 1 ? '' : 'VIP'" 
                        :offset="[-25,5]"
                        :numberStyle="{background:'green','border-radius':'0'}">
                            <!-- <img width="120" alt="logo" :src="item.contestLogo" /> -->
                            <img width="150" height="110" alt="logo" src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1660005742,3398689291&fm=200&gp=0.jpg" />
                        </a-badge>
                    </a-list-item-meta>

                </a-list-item>
            </a-list>
        </a-layout-content>
    </a-layout>
</template>
<script>
import { getListContest } from "@/utils/contestList"


export default {
  data() {
    return {
      listData: [],
      pagination: {
        onChange: page => {
          console.log(page);
        },
        pageSize: 10
      },
      time: [],
      timerCount:[]
    };
  },
  created() {
    this.getListContest('')
  },
  methods: {
    getListContest(data) {
        getListContest(this, data)
    },
    getMyContest(){
        getListContest(this)
    }
  }
};
</script>
